}

.flip-wrap {
    max-width: 430px;
    min-height: 300px;
    margin: auto;
    position: relative;
}

.flip-item {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-transition: 0.4s all ease;
    -o-transition: 0.4s all ease;
    transition: 0.4s all ease;
}

.flip-item.off {
    -webkit-transform: perspective(1000px) rotateY(-180deg);
    transform: perspective(1000px) rotateY(-180deg);
    pointer-events: none;
    opacity: 0;
    visibility: hidden;
}

.flip-item.on {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: perspective(1000px) rotateY(180deg);
    transform: perspective(1000px) rotateY(180deg);
}

.flip-item.on {
    -webkit-transform: perspective(1000px) rotateY(0deg);
    transform: perspective(1000px) rotateY(0deg);
    pointer-events: all;
    opacity: 1;
    visibility: visible;
}

