    background-image: -o-linear-gradient(left, #01a566, #00a193, #0098ba, #008bd1, #2178cf);
    background-image: linear-gradient(to right, #01a566, #00a193, #0098ba, #008bd1, #2178cf);
}

.site-header.is-sticky {
