    background-color: #18a566;
    border-radius: 5px;
    color: #fff;
    text-align: center;
    padding: 0 22px;
}

.nav-bars {
    display: none;
    margin-top: 15px;
    width: 18px;
    height: 22px;
    position: relative;
    float: right;
}

.nav-bars:before,
.nav-bars:after {
    display: block;
    width: 100%;
    height: 1px;
    background: #fff;
    content: '';
    left: 0;
    position: absolute;
    top: 30%;
    -webkit-transform: translate3d(0, -50%, 0);
    transform: translate3d(0, -50%, 0);
    -webkit-transition: 0.3s all ease;
    -o-transition: 0.3s all ease;
    transition: 0.3s all ease;
}

.nav-bars:after {
    webkit-transform: translate3d(0, 570%, 0);
    -webkit-transform: translate3d(0, 570%, 0);
            transform: translate3d(0, 570%, 0);
}

.nav-bars span {
    height: 1px;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background: #fff;
    -webkit-transition: 0.3s all ease;
    -o-transition: 0.3s all ease;
    transition: 0.3s all ease;
}

body.menu-open .nav-bars:before {
    -webkit-transform: translate3d(0, -50%, 0) rotate(45deg);
    transform: translate3d(0, -50%, 0) rotate(45deg);
}

body.menu-open .nav-bars:after {
    -webkit-transform: translate3d(0, -50%, 0) rotate(-45deg);
    transform: translate3d(0, -50%, 0) rotate(-45deg);
}

body.menu-open .nav-bars span {
    opacity: 0;
}

