            box-shadow: 0 2px 35px 0 rgba(0, 0, 0, 0.15), 0 1px 0 0 rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
    border-radius: 5px;
    margin-bottom: 80px;
    min-height: 450px;
    padding-left: 200px;
    overflow: hidden;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -webkit-transition: all 0.3s ease 0s;
}

.left-register {
    position: absolute;
    top: 0;
    left: 0;
    width: 223px;
    height: 100%;
    background-color: #010029;
    border-radius: 5px 0 0 5px;
    padding: 53px 20px;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -webkit-transition: all 0.3s ease 0s;
    z-index: 100;
}

.left-register ul {
    margin: 0;
    padding: 0;
    list-style: none;
    position: relative;
}

.left-register ul:before {
    content: '';
    display: block;
    width: 1px;
    height: 99%;
    margin-top: 2px;
    position: absolute;
    top: 0;
    left: 16px;
    background: rgba(231, 231, 231, 0.2);
}

.left-register ul:after {
    background: #fff;
    content: '';
    height: 1px;
    width: 0px;
    -o-transition: all 0.5s ease 0s;
    transition: all 0.5s ease 0s;
    -moz-transition: all 0.5s ease 0s;
    -webkit-transition: all 0.5s ease 0s;
    display: block;
    position: absolute;
    top: 20px;
    left: 16px;
}

.left-register ul li {
    padding-top: 37px;
    line-height: 33px;
    position: relative;
    z-index: 3;
}

.left-register ul li:first-child {
    padding-top: 0;
}

.left-register .icon {
    display: inline-block;
    vertical-align: middle;
    width: 33px;
    height: 33px;
    border: 1px solid rgba(231, 231, 231, 0.2);
    border-radius: 50%;
    background-color: #010029;
    color: rgba(231, 231, 231, 0.2);
    text-align: center;
    line-height: 30px;
    font-size: 16px;
    position: relative;
    margin-right: 10px;
    z-index: 10;
}

.left-register .icon img {
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
}

.left-register ul li a {
    color: rgba(255, 255, 255, 0.5);
}

.left-register ul li .label span {
    display: none;
}

.left-register li.complete a {
    color: #fff;
}

.left-register li.complete .icon {
    border: 1px solid #18a566;
    background-color: #010029;
    color: #18a566;
}

.right-regsiter {
    height: 100%;
    position: relative;
}

.register-form-title {
    font-size: 20px;
    line-height: 42px;
    margin-bottom: 10px;
    padding-bottom: 10px;
}

.input-field label {
    display: block;
    font-weight: bold;
    line-height: 17px;
    margin-bottom: 14px;
}

.input-field .instruction {
    display: block;
    color: #9b9b9b;
    line-height: 24px;
    margin-top: 10px;
}

.input-field .instruction:empty {
    display: none;
}

.input-field .error,
.input-field .warning {
    display: block;
    color: #f00;
    font-size: 12px;
    font-style: italic;
}

.input-field .error:empty,
.input-field .warning:empty {
    display: none;
}

.right-regsiter fieldset {
    margin-bottom: 10px;
}

.right-regsiter fieldset h3 {
    color: #4f545a;
    opacity: 0.5;
    font-size: 12px;
    font-weight: bold;
    line-height: 42px;
    text-transform: uppercase;
    margin-bottom: 17px;
    position: relative;
}

.right-regsiter fieldset h3:before {
    content: '';
    width: 100%;
    height: 1px;
    display: block;
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -1px;
    background: #e4e4e4;
}

.right-regsiter fieldset h3 span {
    display: inline-block;
    padding-right: 26px;
    background: #fff;
    position: relative;
    z-index: 2;
}

.contact-section .submit-btn {
    border-radius: 3px;
    border-width: 0;
    line-height: 40px;
    height: 40px;
    text-align: center;
    color: #fff;
    width: auto;
    display: inline-block;
    cursor: pointer;
}

.contact-section .submit-btn:hover {
    background: #158a57;
}

.contact-section .input-field,
.right-regsiter .input-field {
    width: 100%;
    margin-right: 15px;
    display: inline-block;
    vertical-align: top;
    margin-bottom: 30px;
}

.contact-section .input-field.hidden-field,
.right-regsiter .input-field.hidden-field {
    display: none;
}
.contact-section .input-field .required,
.right-regsiter .input-field .required {
    color: red;
}

.right-regsiter .input-field-check-box {
    display: none;
}

.input-field-check-box-container {
    position: relative;
    padding-left: 20px;
    margin-bottom: 12px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.input-field-check-box-mark {
    position: absolute;
    top: 0;
    left: 0;
    height: 12px;
    width: 12px;
    background-color: #eee;
}

/* On mouse-over, add a grey background color */

.input-field-check-box-container:hover input ~ .input-field-check-box-mark {
    background-color: #ccc;
    border-radius: 2px;
    padding: 2px;
}

/* When the checkbox is checked, add a blue background */

.input-field-check-box-container input:checked ~ .input-field-check-box-mark {
    background-color: #18a566;
    padding: 2px;
}

/* Create the checkmark/indicator (hidden when not checked) */

.input-field-check-box-mark:after {
    content: '';
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */

.input-field-check-box-container input:checked ~ .input-field-check-box-mark:after {
    display: block;
}

/* Style the checkmark/indicator */

.input-field-check-box-container .input-field-check-box-mark:after {
    left: 4px;
    width: 2px;
    height: 5px;
    border: solid white;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.right-regsiter .input-field-check-box-span {
    vertical-align: text-bottom;
}

.contact-section fieldset {
    padding-left: 0;
}

.contact-section .input-field {
    width: 100%;
}

.contact-section .input-field input,
.right-regsiter .input-field input {
    height: 40px;
    width: 100%;
    border: 1px solid #e4e4e4;
    border-radius: 5px;
    display: table-cell;
    background-color: #ffffff;
}

.contact-section .input-field textarea {
    width: 100%;
    border: 1px solid #e4e4e4;
    border-radius: 5px;
    background-color: #ffffff;
    min-height: 200px;
}

.contact-section .input-field textarea:focus,
.contact-section .input-field input:focus,
.right-regsiter .input-field input:focus {
    border-color: #18a566;
}

.right-regsiter .field-full .input-field {
    width: 100%;
    margin-right: 0;
    max-width: 480px;
    display: block;
}

.right-regsiter .input-info {
    padding-bottom: 10px;
}

.right-regsiter .input-info .input-field {
    width: 100%;
    margin-right: 0;
    display: block;
    margin-bottom: 16px;
}

.right-regsiter .input-info .input-field label {
    display: inline-block;
    width: 155px;
    vertical-align: top;
    margin: 0;
    line-height: 24px;
}

.right-regsiter .input-info .input-field .info {
    display: inline-block;
    vertical-align: top;
    line-height: 24px;
}

.right-regsiter .input-info .input-field .info:before {
    content: ':';
    margin-right: 10px;
}

.selectbox {
    width: 100%;
    height: 40px;
    background: #fff;
    border-radius: 5px;
    position: relative;
}

.selectbox select {
    width: 100%;
    height: 100%;
    background: none;
    border: 1px solid #e4e4e4;
    border-radius: 5px;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    padding: 0 15px;
    position: relative;
    outline: none;
}

.selectbox select:focus {
    border-color: #18a566;
}

.error-field.select-field select {
    border-color: #f00;
}

.selectbox:before {
    content: '';
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-top: 5px solid #a9a9a9;
    position: absolute;
    top: 50%;
    right: 15px;
    margin-top: -2px;
}

.datepicker-field {
    background: #fff;
    position: relative;
}

.datepicker-field:before {
    content: '\f073';
    font-family: FontAwesome;
    position: absolute;
    right: 15px;
    top: 38px;
}

.right-regsiter .datepicker-field input {
    background: none;
    position: relative;
    z-index: 2;
}

.regsiter-form-action {
    text-align: center;
    border-top: 1px solid #e4e4e4;
    padding-top: 25px;
}

.regsiter-form-action.not-border {
    border-top: 0;
}

.regsiter-form-action .pri-button,
.regsiter-form-action .square-button {
    height: 40px;
    line-height: 40px;
    font-weight: normal;
    font-size: 14px;
    padding: 0 20px;
}

.regsiter-form-action .square-button {
    line-height: 36px;
    padding: 0 30px;
    float: left;
}

.register-gray-box {
    border: 1px solid #dfe3ff;
    border-radius: 5px;
    background-color: #f5f6ff;
    padding: 26px;
    margin-bottom: 28px;
    margin-top: 30px;
}

.register-gray-box ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.register-gray-box > ul > li {
    float: left;
    width: 17%;
}

.register-gray-box > ul > li:nth-child(1) {
    width: 24%;
    padding-top: 5px;
}

.register-gray-box > ul > li:nth-child(2) {
    width: 38%;
}

.register-gray-box > ul > li:nth-child(3) {
    width: 21%;
}

.register-gray-box strong {
    display: block;
    margin-bottom: 8px;
}

.register-gray-box ul ul li {
    margin-bottom: 8px;
}

.register-gray-box ul ul li:before {
    content: '';
    display: inline-block;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #18a566;
    margin-right: 5px;
    vertical-align: middle;
}

.register-gray-box .metrics {
    font-size: 18px;
    color: #18a566;
    line-height: 24px;
    font-weight: bold;
    display: block;
    margin-bottom: 12px;
}

.register-step {
    padding: 30px 50px;
    -o-transition: all 0.5s ease 0s;
    transition: all 0.5s ease 0s;
    -moz-transition: all 0.5s ease 0s;
    -webkit-transition: all 0.5s ease 0s;
    width: 100%;
    min-height: 450px;
    position: relative;
}

.register-step.step-hide {
    position: absolute;
    opacity: 0;
    visibility: hidden;
}

.regsiter-form-action .pri-button {
    position: relative;
}

.regsiter-form-action .pri-button span {
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -webkit-transition: all 0.3s ease 0s;
    position: relative;
    display: inline-block;
}

.regsiter-form-action .pri-button:before {
    content: '\f110';
    font-family: FontAwesome;
    position: absolute;
    top: 0;
    left: 50%;
    margin-left: -5px;
    z-index: 10;
    -webkit-animation: spin 1.5s linear infinite;
    /* Safari */
    animation: spin 1.5s linear infinite;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -webkit-transition: all 0.3s ease 0s;
    opacity: 0;
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.step-register-fullbox.loading.next-btn-click .regsiter-form-action .pri-button {
    cursor: default;
    opacity: 0.6;
}

.step-register-fullbox.loading.next-btn-click .regsiter-form-action .pri-button span {
    opacity: 0;
    -webkit-transform: scale(2.2);
    -ms-transform: scale(2.2);
        transform: scale(2.2);
}

.step-register-fullbox.loading.next-btn-click .regsiter-form-action .pri-button:before {
    opacity: 1;
}

